/* 1rem = 12px */
html,body{
  width: 100%;
  height: 100%;
  font: 12px/1.5 'Sarasa Gothic SC Regular', '', sans-serif;
}

.count-wrapper{
  width: 50rem;
  height: 20rem;
  margin: 10rem auto;
}
.count-select{
  width: 10rem;
  margin-right: 1rem;
}
.count-btn{
  margin: 0 0.5rem;
}

/* Person */
.usernameInput{
  margin-bottom: .5rem;
  width: 50%;
}
.ageInput{
  margin-bottom: .5rem;
  width: 50%;
}
.userBtn{
  margin-left: 2rem;
}